import React, { useEffect } from 'react';
import DarkModeOutlinedIcon from '@mui/icons-material/DarkModeOutlined';
import WbSunnyOutlinedIcon from '@mui/icons-material/WbSunnyOutlined';
import styles from './NavBar.module.css';
import { ReactComponent as Facebook } from '../Assets/Framefacebook.svg';
import { ReactComponent as Instagram } from '../Assets/FrameInstagram.svg';
import { ReactComponent as Linkedin } from '../Assets/FrameLinkedin.svg';
import { ReactComponent as Twitter } from '../Assets/FrameTwiter.svg';
import { langTokens, useTheme } from '../ThemeContext';
import AnchorLink from 'react-anchor-link-smooth-scroll';

const NavBar = ({ selectedPage, setSelectedPage }) => {
  // LANGUAGE FUNCTION
  // ###############################################

  const { darkTheme, language, setLanguage, toggleTheme } = useTheme();
  function languageSelection({ target }) {
    const text = target.innerText;
    setLanguage(text);
  }
  const langVar = langTokens(language)['navBar'];

  // ###############################################

  // NAV BAR LINK SMOOTH SCROLL

  // ###############################################

  const Link = ({ page, selectedPage, setSelectedPage }) => {
    const lowerCasePage = page.toLowerCase();
    return (
      <AnchorLink
        className={`
        ${selectedPage === lowerCasePage ? styles.active : ''}
        ${styles.link}`}
        href={`#${lowerCasePage}`}
        onClick={() => {
          setSelectedPage(lowerCasePage);
        }}
      >
        {langVar[page]}
      </AnchorLink>
    );
  };
  // ###############################################

  // COLOR CHANGE FROM THE LANGUAGE MENU

  // ###############################################

  useEffect(() => {
    const toggle = language === 'En' ? 'Pt' : 'En';
    const active = document.querySelector(`#${language}`);
    const inactive = document.querySelector(`#${toggle}`);

    active.classList.toggle('active');
    inactive.classList.toggle('active');
  }, [language]);

  // ###############################################

  return (
    <>
      <header className={styles.container}>
        {/* LIGHT/DARK MODE BAR */}
        <div className={styles.containerFluid}>
          <div className={styles.settings}>
            {darkTheme ? (
              <DarkModeOutlinedIcon
                onClick={toggleTheme}
                className={styles.theme}
              />
            ) : (
              <WbSunnyOutlinedIcon
                onClick={toggleTheme}
                className={styles.theme}
              />
            )}

            {/* LANGUAGE SETTINGS BAR */}

            <div className={styles.language}>
              <p onClick={languageSelection} className={styles.button} id="En">
                En
              </p>
              <span>|</span>
              <p
                onClick={languageSelection}
                className={`${styles.button} active`}
                id="Pt"
              >
                Pt
              </p>
            </div>
          </div>

          {/* AVATAR */}

          <div className={styles.profile}>
            <p>
              Alexandre <span>Toulios</span>
            </p>
          </div>

          {/* NAV BAR */}

          <nav className={styles.navBar}>
            <Link
              page="home"
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
            <hr />

            <Link
              page="about"
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
            <hr />
            <Link
              page="skills"
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
            <hr />
            <Link
              page="portfolio"
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
            <hr />
            <Link
              page="contact"
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
          </nav>

          {/* SOCIAL MEDIA */}

          <div className={styles.socialMedia}>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://www.facebook.com/alexandre.toulios"
            >
              <Facebook className={styles.socialIcon} />
            </a>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://www.instagram.com/alesanfrancisco/"
            >
              <Instagram className={styles.socialIcon} />
            </a>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://www.linkedin.com/in/alexandre-toulios-180b90256/"
            >
              <Linkedin className={styles.socialIcon} />
            </a>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://twitter.com/LehToulios"
            >
              <Twitter className={styles.socialIcon} />
            </a>
          </div>
        </div>
      </header>
    </>
  );
};

export default NavBar;
